@import url(https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Condensed:wght@700&display=swap);
body{margin:0;padding:0;font-family:'Roboto', sans-serif;color:#ffffff;font-size:14px;background-color:#26282C}h1,h2,h3,h4,h5,h6,p{margin:0;padding:0}.wrapper{position:relative;height:94vh;cursor:default}

.button{text-align:center;margin-top:20px}

.input{position:relative;margin-top:30px;width:100%}.input__field{font-size:15px;width:93%;border:1px solid rgba(255,255,255,0.1);border-radius:5px;color:rgba(255,255,255,0.9);background:0;outline:0;padding:15px 10px;transition:all 0.2s}.input__field:-webkit-autofill,.input__field:-webkit-autofill:hover,.input__field:-webkit-autofill:focus,.input__field:-webkit-autofill:active{-webkit-transition:background-color 5000s ease-in-out 0s;transition:background-color 5000s ease-in-out 0s;-webkit-text-fill-color:#ffffff}.input__field:hover{border-color:rgba(255,255,255,0.6)}.input__field:focus{border-color:#fff}.input__field--error{border-color:rgba(206,39,39,0.6)}.input__field--error:hover{border-color:rgba(206,39,39,0.6)}.input__field--error:focus{border-color:#ce2727}.input__label{font-size:17px;position:absolute;top:-25px}.input__label--error{color:#ce2727}.input__helper{cursor:pointer;outline:none;border:none;background:none;color:rgba(255,255,255,0.4);font-size:11px;transition:color 0.6s}.input__helper__box{position:absolute;right:0;top:-22px}.input__helper:hover,.input__helper:focus{color:rgba(255,255,255,0.6)}.password--eye{position:absolute;top:13px;right:13px;cursor:pointer}

.auth--logo{display:flex;justify-content:center;align-items:center;margin:10px 0}.auth--logo__text{color:#ffffff;font-size:22px;font-family:'Roboto Condensed', sans-serif;margin-left:3px}

.box{position:absolute;text-align:center;margin:0 auto;top:25%;left:0;right:0;background-color:#1a1b1c;width:430px;padding:15px 0;border-radius:20px;box-shadow:0 0 19px 6px rgba(0,0,0,0.29)}.box__container{width:100%;max-width:320px;margin:0 auto}.box--large{position:relative;text-align:left;top:0;margin-top:50px;width:1400px;min-height:600px}.box--large__container{max-width:100%;margin:0}.box__footer{height:50px}

.link{margin-top:20px;font-size:14px}.link__field{color:#1a76d5;text-decoration:none;position:relative;margin-left:5px}.link__field:before{content:"";width:0;height:1px;background-color:#1a76d5;position:absolute;bottom:-2px;left:0;transition:width 0.3s}.link__field:hover:before{width:100%}

.errors{display:flex;text-align:left;align-items:center;border:1px solid #cd2c2f;border-radius:5px;margin:15px 0;padding:5px;color:#cd2c2f}.errors__item{margin-left:3px;margin-bottom:3px;font-size:13px;color:#cd2c2f}

.note__check__box{cursor:pointer;width:20px;height:20px;border:2px solid #939393;border-radius:50%;position:relative}.note__check__box:active,.note__check__box:active .note__check{opacity:1 !important;background-color:#939393 !important;fill:#000000 !important}.note__check__box:hover .note__check{opacity:1 !important}.note__check{transition:all 0s !important;fill:#b2b2b2 !important;opacity:0 !important;position:absolute !important;top:14% !important;left:19% !important;width:13px !important;height:13px !important}.note__check__box__completed{background-color:#788cde !important;border-color:#788cde !important}.note__check__box__completed:active,.note__check__box:active .note__check{background:none !important;border-color:#939393 !important;fill:#242426 !important}.note__check__completed{opacity:1 !important;fill:#000000 !important}.note__text{font-size:15px}.note__text__completed{text-decoration:line-through}.note__important{cursor:pointer}.note__important:hover{fill:#788cde !important}.note__important:active{fill:#51609d !important}.note__important__active,.note__important__active:hover{fill:#616fab !important}.note__important__active:active{fill:#51609d !important}.note__delete{cursor:pointer;fill:#ea2227 !important}.note__delete:hover{fill:#ff161b !important}.note__delete:active{fill:#9A181f !important}

.note--box{display:flex;justify-content:space-between;align-items:center;padding:17px 15px;background-color:#242426;margin-top:3px;border-radius:5px}.note--box:hover{background-color:#292929}.note--box__left{display:flex;align-items:center}.note--box__icon__box{display:flex;cursor:pointer;height:20px;position:relative}.note--box__right--icon{display:flex}.note--box__body{max-width:1205px;word-break:break-word;padding-left:15px}

.main--input{border:none;outline:none;color:#ffffff;padding:5px;font-size:16px;background:none;display:block;width:1111px;resize:none}

.main{display:flex}.main__nav{width:200px}.main__content{margin:0 20px 20px 20px;width:100%}.main__name__block{display:flex;justify-content:center;align-items:center;margin:20px 0 30px 0}.main__name__block p{margin-left:5px}

